* {
  box-sizing: border-box;
}
*::after {
  box-sizing: border-box;
}
*::before {
  box-sizing: border-box;
}

body, main {
  overflow-x: hidden;
  height: 100%;
}

@font-face {
  font-family: "ProximaNova-Regular";
  src: url("/fonts/ProximaNova-Regular.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova-SemiBold";
  src: url("/fonts/ProximaNova-Semibold.ttf") format("ttf");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
html, body {
  height: 100%;
  overflow: hidden;
}

html, body, a, button {
  font: 400 16px/24px "ProximaNova-Regular", sans-serif;
  color: rgba(0, 0, 0, 0.8);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1440px;
  height: 100%;
  width: 100%;
  margin: 0 auto;
}

body, ul, ol, th, td, button, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  border: 0;
  background-color: transparent;
}
button:focus {
  outline: none;
}
button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: currentColor;
}
a:focus {
  outline: none;
}
a:active {
  outline: 0;
}
a:hover {
  outline: 0;
}

li {
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
  border: 0;
}

audio {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}

canvas {
  display: inline-block;
  vertical-align: baseline;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

video {
  display: inline-block;
  vertical-align: baseline;
}

[hidden] {
  display: none;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b {
  font-weight: bold;
}

strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

small {
  font-size: 80%;
}

sub {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
  bottom: -0.25em;
}

sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
  top: -0.5em;
}

svg {
  flex-shrink: 0;
}
svg:not(:root) {
  overflow: hidden;
}

hr {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
  font-family: monospace, monospace;
  font-size: 1em;
}

code {
  font-family: monospace, monospace;
  font-size: 1em;
}

kbd {
  font-family: monospace, monospace;
  font-size: 1em;
}

samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

input,
textarea {
  display: block;
  color: inherit;
  font: inherit;
  margin: 0;
  line-height: normal;
}
input::-moz-focus-inner,
textarea::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input:focus, input:focus-visible,
textarea:focus,
textarea:focus-visible {
  outline: none;
}
input::-webkit-outer-spin-button,
textarea::-webkit-outer-spin-button {
  -webkit-appearance: none;
}
input::-webkit-inner-spin-button,
textarea::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input:not([type=checkbox]):not([type=radio]),
textarea {
  appearance: none;
  -webkit-appearance: none;
}

optgroup {
  color: inherit;
  font: inherit;
  margin: 0;
  font-weight: bold;
}

select {
  color: inherit;
  font: inherit;
  margin: 0;
  text-transform: none;
}

textarea {
  overflow: auto;
  resize: none;
}
textarea:focus {
  outline: none;
}

div:focus {
  outline: none;
}

html input[type=button] {
  -webkit-appearance: button;
  cursor: pointer;
}
html input[disabled] {
  cursor: default;
}

input[type=reset] {
  -webkit-appearance: button;
  cursor: pointer;
}

input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled] {
  cursor: default;
}

input[type=checkbox] {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}

input[type=radio] {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}

input[type=number]::-webkit-inner-spin-button {
  height: auto;
}
input[type=number]::-webkit-outer-spin-button {
  height: auto;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]:hover,
input[type=number]:focus {
  -moz-appearance: number-input;
}

input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

legend {
  border: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background-color: #ccc;
}

.card {
  max-width: 768px;
  width: 100%;
  padding: 24px 24px 16px 24px;
  background-color: #fff;
  border-radius: 8px;
}
.card__top {
  display: flex;
  justify-content: space-between;
  padding: 0 0 20px 0;
  border-bottom: 1px solid #e4e4e4;
}
.card__profile {
  display: grid;
  grid-template-columns: auto 1fr 1fr;
  align-items: center;
  gap: 12px;
}
.card__avatar {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  overflow: hidden;
}
.card__avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card__user-name {
  margin: 0 0 2px 0;
}
.card__user-name h4 {
  font: 600 14px/16px "ProximaNova-SemiBold", sans-serif;
}
.card__profile-statistics {
  display: flex;
  align-items: center;
  gap: 12px;
}
.card__statistics-item {
  display: flex;
  flex-direction: column;
  min-width: 64px;
}
.card__statistics-item--font-bold {
  font: 600 14px/16px "ProximaNova-SemiBold", sans-serif;
}
.card__info-statistics p {
  font: 400 14px/16px "ProximaNova-Regular", sans-serif;
  color: rgba(0, 0, 0, 0.4);
}
.card__title-statistics {
  display: flex;
}
.card__title-statistics.roi-statistics::after {
  content: ", %";
  display: block;
}
.card__title-statistics--font-light {
  font: 400 12px/14px "ProximaNova-Regular", sans-serif;
}
.card__title-statistics--green {
  color: #62b435;
}
.card__title-statistics--red {
  color: #f64747;
}
.card__title-statistics--gray {
  color: rgba(0, 0, 0, 0.4);
}
.card__number-statistics {
  margin: 4px 0 0 0;
  padding: 4px 0 0 0;
  border-top: 1px solid #e4e4e4;
  font: 600 14px/16px "ProximaNova-SemiBold", sans-serif;
}
.card__number-statistics--green {
  color: #62b435;
}
.card__number-statistics--red {
  color: #f64747;
}
.card__number-statistics--gray {
  color: rgba(0, 0, 0, 0.4);
}
.card__content {
  position: relative;
}
.card__text-content {
  padding: 20px 0 24px 0;
}
.card__text-content p {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  overflow: hidden;
  color: rgba(0, 0, 0, 0.6);
}
.card__text-content::after {
  content: "";
  position: absolute;
  bottom: 27px;
  left: 0;
  right: 0;
  display: block;
  width: 100%;
  height: 27px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #fff 100%);
  z-index: 1;
}
.card__show-more-btn {
  position: absolute;
  bottom: -44px;
  left: 0;
  right: 0;
  width: 40px;
  height: 40px;
  margin: 0 auto;
  border-radius: 50%;
  box-shadow: 2px 4px 12px 0 rgba(0, 0, 0, 0.08);
  background: #fff url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2011.23%206.23%22%3E%3Cpath%20d%3D%22M.56.56a.63.63%2C0%2C0%2C1%2C.88%2C0L6%2C5.12%2C10.56.56a.63.63%2C0%2C0%2C1%2C.88%2C0%2C.62.62%2C0%2C0%2C1%2C0%2C.87l-5%2C5a.63.63%2C0%2C0%2C1-.88%2C0l-5-5A.62.62%2C0%2C0%2C1%2C.56.56Z%22%20transform%3D%22translate(-0.38%20-0.38)%22%20style%3D%22fill-opacity%3A0.4000000059604645%3Bfill-rule%3Aevenodd%22%2F%3E%3C%2Fsvg%3E") no-repeat center;
  background-size: 11px 6px;
  z-index: 2;
}

@media screen and (max-width: 743px) {
  .card {
    padding: 16px 16px 12px 16px;
  }
  .card__top {
    flex-direction: column;
    padding: 0 0 16px 0;
  }
  .card__profile {
    grid-template-columns: auto 1fr;
    grid-template-areas: "avatar user-name" "avatar statistics";
    gap: 2px 8px;
  }
  .card__avatar {
    grid-area: avatar;
  }
  .card__user-name {
    grid-area: user-name;
    margin: 0;
  }
  .card__statistics {
    grid-area: statistics;
  }
  .card__info-statistics span {
    display: none;
  }
  .card__title-statistics {
    align-items: center;
  }
  .card__title-statistics.roi-statistics::after {
    display: none;
  }
  .card__statistics-item {
    flex-direction: row;
  }
  .card__statistics-item:not(:has(.roi-statistics)) .card__title-statistics {
    display: none;
  }
  .card__profile-statistics {
    gap: 13px;
  }
  .card .roi-statistics {
    margin: 0 5px 0 0;
  }
  .card__number-statistics {
    margin: 0;
    padding: 0;
    border: none;
  }
  .card__statistics-item {
    min-width: auto;
  }
  .card__statistics-item:last-of-type {
    margin: 0 0 0 auto;
  }
  .card__statistics-item:not(:has(.profit-statistics)) .card__number-statistics {
    font: 400 14px/16px "ProximaNova-Regular", sans-serif;
  }
  .card__statistics-item:nth-child(-n+3) {
    position: relative;
  }
  .card__statistics-item:nth-child(-n+3)::after {
    content: "/";
    position: absolute;
    top: 0;
    right: -8px;
    color: #e4e4e4;
  }
  .card__profile-info {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .card__text-content {
    padding: 16px 0;
  }
  .card__text-content::after {
    bottom: 20px;
  }
  .card__show-more-btn {
    bottom: -22px;
  }
}